import React, { useState } from "react";
import axios from 'axios';
import FormInsertNote from "./app/modules/FormInsertNote.tsx";
import MenuBar from "./app/common/MenuBar.tsx";
import DataTable from "./app/common/DataTable.tsx";
import Footer from "./app/common/Footer.tsx";
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';

export const Home = (props) => {
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [description, setDescription] = useState('');
    const [submittedData, setSubmittedData] = useState<{ title: string, content: string, description: string } | null>(null);
    const [refreshData, setRefreshData] = useState(false);

    const Item = styled(Paper)(({ theme }) => ({
        backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
        ...theme.typography.body2,
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
      }));

    const handleTitleChange = (event) => {
        setTitle(event.target.value);
    };
    
    const handleContentChange = (event) => {
      setContent(event.target.value);
    };

    const handleDescriptionChange = (event) => {
        setDescription(event.target.value);
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        const data = { title, content, description };
        axios.post('https://savebrain.net/api/brain-note/create', data)
            .then(response => {
                console.log('Data submitted successfully:', response.data);
                setSubmittedData(data);
                setRefreshData(!refreshData); // Toggle refreshData to trigger re-render of DataTable
            })
            .catch(error => {
                console.error('Error submitting data:', error);
            });
    };

    return (
  
            <Box sx={{ flexGrow: 1 }}>
                <Grid container spacing={2} columns={16}>
                    <Grid item xs={16}>
                        <MenuBar />
                    </Grid>
                    <Grid item xs={16}>
                    
                    </Grid>
                    <Grid item xs={16}>
                        <p>Bạn cần note thông tin gì mời bạn nhập form dưới đây:</p>
                    </Grid>
                    <Grid item xs={16}>
                        <FormInsertNote
                            title={title}
                            content={content}
                            description={description}
                            onTitleChange={handleTitleChange}
                            onContentChange={handleContentChange}
                            onDescriptionChange={handleDescriptionChange}
                            onSubmit={handleSubmit}
                        />
                    </Grid>
                    <Grid item xs={16}>
                        {submittedData && (
                            <div>
                                <h2>Thông tin bạn mới nhập:</h2>
                                <p><strong>Title:</strong> {submittedData.title}</p>
                                <p><strong>content:</strong> {submittedData.content}</p>
                                <p><strong>Description:</strong> {submittedData.description}</p>
                            </div>
                        )}
                        <DataTable refreshData={refreshData} />
                    </Grid>
                </Grid>
                <Grid container direction="column" style={{ minHeight: '100vh' }}>
                    <Grid item xs={16}>
                    {/* Nội dung chính của trang */}
                    </Grid>
                    <Grid item>
                    <Footer />
                    </Grid>
                </Grid>
            </Box>
    
    );
};

export default Home;
